

.logo-img{
  height:52px;
  width:auto;
  transition:transform .25s ease;
}
.logo-img:hover{ transform:scale(1.05); }


.feature-wrapper > div{
  flex:1;
}
/* NAV LINKS */
.nav-item-custom{
  display:flex;
  align-items:center;
  justify-content:space-between;
  gap:6px;
  cursor:pointer;
  font-weight:600;
  font-size:.95rem;
  padding:6px 0;
  white-space:nowrap;
}

/* DROPDOWN */
.custom-dropdown{
  background:#1b6dc1;
  border-radius:12px;
  padding:14px;
  margin-top:10px;
}
.dropdown-title{
  color:#fff;
  border-bottom:1px solid rgba(255,255,255,.3);
  padding-bottom:6px;
  margin-bottom:10px;
  font-size:.9rem;
}
.dropdown-list{
  list-style:none;
  padding:0;
  margin:0;
  display:flex;
  flex-wrap:wrap;
  gap:8px;
}
.dropdown-link{
  display:block;
  padding:7px 12px;
  border-radius:8px;
  font-size:.85rem;
  color:#fff;
  text-decoration:none;
  transition:.25s;
}
.dropdown-link:hover{
  background:#fff;
  color:#2e3192;
  transform:translateX(3px);
}

.btn-signup{
  background:#193968;
  border:none;
  color:#fff;
  border-radius:999px;
  padding:6px 16px;
  font-weight:600;
}
.btn-signup:hover{ background:#2e3192; }

/* DESKTOP */
@media(min-width:992px){
  .custom-dropdown{
    position:absolute;
    min-width:520px;
  }
  .navbar-collapse{ flex-grow:0; }
}

/* MOBILE */
@media(max-width:991px){
  .navbar-collapse{
    background:#fff;
    margin-top:10px;
    padding:18px;
    border-radius:14px;
  }

  .nav-item-custom{
    width:100%;
    padding:12px 0;
    border-bottom:1px solid #eee;
  }

  .custom-dropdown{
    position:static;
    width:100%;
  }

  .auth-buttons{ margin-top:14px; }
  .auth-buttons button{ width:100%; }
}

/* SMALL MOBILE */
@media(max-width:480px){
  .logo-img{ height:44px; }
}



/* ===== AUTH BUTTONS ===== */

.auth-buttons{
  display:flex;
  align-items:center;
  gap:10px;
}

/* base button */
.nav-btn{
  display:flex;
  align-items:center;
  justify-content:center;
  gap:8px;
  height:40px;
  padding:0 18px;
  border-radius:999px;
  font-weight:600;
  font-size:.9rem;
  white-space:nowrap;
  transition:all .25s ease;
}

/* icon */
.nav-btn .btn-icon{
  font-size:13px;
}

/* SIGNUP */
.nav-btn-signup{
  background:linear-gradient(135deg,#193968,#5a7191);
  color:#fff;
  border:none;
  box-shadow:0 4px 14px rgba(46,49,146,.25);
}

.nav-btn-signup:hover{
  transform:translateY(-1px);
  box-shadow:0 6px 18px rgba(46,49,146,.35);
}

/* ===== MOBILE ===== */
@media (max-width:991px){

  .auth-buttons{
    flex-direction:column;
    width:100%;
    margin-top:14px;
  }

  .nav-btn{
    width:100%;
    height:44px;
    font-size:1rem;
  }
}

/* ===== LARGE DESKTOP ===== */
@media (min-width:1400px){
  .nav-btn{
    height:42px;
    padding:0 22px;
    font-size:.95rem;
  }
}

/* ===== APP DOWNLOAD SECTION ===== */

.app-download-section{
  background:#ffffff;
  color:#212529;
  position:relative;
  overflow:hidden;
}

/* heading */
.banner-head{
  font-size:2.4rem;
  line-height:1.3;
  color:#2d2d2d;
  font-weight:700;
}

/* gradient text */
.highlight-text{
    color: #cc9840;
  font-weight:800;
}

/* badges */
.store-badge{
  height:50px;
  transition:transform .25s ease;
  cursor:pointer;
}

.store-badge:hover{
  transform:scale(1.06);
}

/* floating image */
.floating-img{
  width:100%;
  max-width:460px;
  height:auto;
  object-fit:contain;
  animation:float 4s ease-in-out infinite;
  filter:drop-shadow(0 12px 22px rgba(0,0,0,.12));
}

/* floating animation */
@keyframes float{
  0%,100%{ transform:translateY(0); }
  50%{ transform:translateY(-14px); }
}

/* background blur circle */
.download-bg-gradient{
  position:absolute;
  width:520px;
  height:520px;
  background:radial-gradient(circle, rgba(46,49,146,.15), transparent 70%);
  filter:blur(100px);
  top:-60px;
  right:-120px;
  z-index:0;
}

/* text spacing */
.app-download-section p{
  font-size:1rem;
  line-height:1.7;
}

/* ===== RESPONSIVE ===== */

@media (max-width:992px){
  .banner-head{
    font-size:2rem;
    text-align:center;
  }
}

@media (max-width:768px){
  .banner-head{ font-size:1.7rem; }
  .store-badge{ height:45px; }
  .floating-img{ max-width:380px; }
}

@media (max-width:576px){
  .banner-head{ font-size:1.45rem; }
  .store-badge{ height:42px; }
  .floating-img{ max-width:320px; }
}

@media (max-width:400px){
  .banner-head{ font-size:1.25rem; }
  .store-badge{ height:38px; }
}




.footer-main{
  background:linear-gradient(135deg,#193968,#122845);
}



.footer-list li{
  margin-bottom:8px;
  color:#d1d5db;
  cursor:pointer;
  transition:.25s;
}
.footer-list li:hover{
  color:#ffd700;
  transform:translateX(5px);
}

.store-btn{
  transition:.25s;
  cursor:pointer;
}
.store-btn:hover{
  transform:scale(1.05);
}

.footer-social img{
  opacity:.85;
  transition:.25s;
}
.footer-social img:hover{
  opacity:1;
  transform:translateY(-3px);
}



.footer-social img{
  width:20px;
  height:20px;
  filter:invert(1) brightness(2);
  opacity:.8;
  transition:.25s ease;
}

.social-icon{
  width:40px;
  height:40px;
  display:flex;
  align-items:center;
  justify-content:center;
  border-radius:50%;
  background:rgba(255,255,255,.08);
  transition:.25s;
}

.social-icon:hover{
  background:#ffd700;
  transform:translateY(-4px);
}

.social-icon:hover img{
  filter:none;
}
.gradient-text{
  color:#cc9840;
  font-weight:800;
  text-shadow:0 2px 10px rgba(204,152,64,.25);
}
/* ===== PERMANENT WHITE NAVBAR ===== */

.main-navbar{
  background:#ffffff !important;
  box-shadow:0 6px 20px rgba(0,0,0,.08);
}

/* nav links */
.nav-link-custom{
  color:#193968 !important;
  font-weight:600;
}

.nav-link-custom:hover{
  color:#cc9840 !important;
}

/* login button */
.nav-btn-login{
  color:#193968 !important;
  border:1.5px solid #193968 !important;
  background:transparent !important;
}

.nav-btn-login:hover{
  background:#193968 !important;
  color:#ffffff !important;
}

/* signup button */
.nav-btn-signup{
  background:linear-gradient(135deg,#193968,#5a7191) !important;
  color:#fff !important;
}

.nav-btn-signup:hover{
  background:#cc9840 !important;
}

/* mobile menu */
@media (max-width:991px){
  .navbar-collapse{
    background:#ffffff !important;
  }
}

.main-navbar .nav-link-custom:hover{
  color:#cc9840 !important;
}

/* ===== FINAL RESPONSIVE SLIDER ===== */

.main-slider{
  width:100%;
  height:clamp(420px,78svh,820px);
  position:relative;
  overflow:hidden;
}

.slider-track{
  display:flex;
  height:100%;
  transition:transform .8s ease-in-out;
}

.slide{
  min-width:100%;
  height:100%;
  position:relative;
}

.slide-img{
  width:100%;
  height:100%;
  object-fit:cover;
  object-position:center;
  filter:brightness(.75);
  transform:none !important;
}


.slide{
  overflow:hidden;
}




.slide-overlay h1{
  font-size:clamp(28px,5vw,60px);
  font-weight:800;
  letter-spacing:.5px;
  text-shadow:
    0 4px 18px rgba(0,0,0,.35),
    0 2px 40px rgba(255,255,255,.15);
}

.slide-overlay p{
  font-size:clamp(15px,1.4vw,20px);
  margin-top:12px;
  letter-spacing:.3px;
  color:#e5e7eb;
}


/* soft overlay */
.slide::before{
  content:"";
  position:absolute;
  inset:0;
  background:linear-gradient(to bottom, rgba(0,0,0,.35), rgba(0,0,0,.35));
  z-index:1;
}

/* content */
.slide-overlay{
  position:absolute;
  inset:0;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:flex-start;
  padding-left:clamp(20px,8vw,120px);
  padding-right:20px;
  color:#fff;
  z-index:2;
}

.slide-overlay h1{
  font-size:clamp(26px,4.5vw,54px);
  font-weight:700;
  line-height:1.2;
  margin-bottom:12px;
}

.slide-overlay p{
  font-size:clamp(14px,1.2vw,20px);
  color:#e5e7eb;
}

/* dots */
.slider-dots{
  position:absolute;
  bottom:18px;
  left:50%;
  transform:translateX(-50%);
  display:flex;
  gap:10px;
  z-index:3;
}

.dot{
  width:9px;
  height:9px;
  border-radius:50%;
  background:#ffffff80;
  cursor:pointer;
  transition:.3s;
}

.dot.active{
  background:#fff;
  transform:scale(1.3);
}

/* mobile center text */
@media(max-width:768px){
  .slide-overlay{
    align-items:center;
    text-align:center;
    padding:0 18px;
  }
}


@media (max-width:991px){

  .auth-buttons{
    gap:12px;
    margin-top:18px;
  }

  .nav-btn{
    height:48px;
    font-size:1rem;
    border-radius:14px;
  }

  .nav-btn-login{
    color:#193968 !important;
    border:2px solid #193968 !important;
    background:#f8fafc !important;
  }

  .nav-btn-login:hover{
    background:#193968 !important;
    color:#ffffff !important;
  }

  .nav-btn-signup{
    background:linear-gradient(135deg,#193968,#5a7191) !important;
    color:#fff !important;
  }

  .nav-btn-signup:hover{
    background:#cc9840 !important;
  }
}




@media(max-width:768px){

